:root {
  /* 폰트 패밀리 */
  --font-primary: 'NanumBarunGothic', sans-serif;

  /* 색상 팔레트 */
  --color-main: #C92B2A;
  --color-main-hover: #A52421;
  --color-dark-gray: #3E3E3E;
  --color-light-gray: #E3E3E3;
  --color-guide: #919191;
  --color-success: #48B669;
  --color-black: #000000;
  --color-white: #FFFFFF;

  /* 자주 사용하는 폰트 크기 */
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-xl: 24px;
  --font-size-2xl: 32px;

  /* 자주 사용하는 간격 */
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-base: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;

  /* 자주 사용하는 테두리 반경 */
  --border-radius-sm: 4px;
  --border-radius-base: 8px;
  --border-radius-lg: 16px;
  --border-radius-xl: 22px;
  --border-radius-full: 9999px;



  /* 푸터 높이 */
  --footer-height: 9vh;

  /* 화면 사이즈 */
  --screen-max-width: 400px;
}

/* 유틸리티 클래스 */
.text-main {
  color: var(--color-main);
}

.text-dark {
  color: var(--color-dark-gray);
}

.text-light {
  color: var(--color-light-gray);
}

.text-guide {
  color: var(--color-guide);
}

.text-success {
  color: var(--color-success);
}

.bg-main {
  background-color: var(--color-main);
}

.bg-dark {
  background-color: var(--color-dark-gray);
}

.bg-light {
  background-color: var(--color-light-gray);
}

.bg-success {
  background-color: var(--color-success);
}

/* 버튼 스타일 */
.btn {
  border-radius: var(--border-radius-base);
  padding: var(--spacing-sm) var(--spacing-base);
  transition: background-color 0.3s ease;
}

.btn-main {
  background-color: var(--color-main);
  color: white;
}

.btn-main:hover {
  background-color: var(--color-main-hover);
}

/* 원형 스타일 */
.circle {
  border-radius: var(--border-radius-full);
  border: 1px solid var(--color-light-gray);
}

.circle-filled {
  border-radius: var(--border-radius-full);
  background-color: var(--color-light-gray);
}